import React, { useState, useEffect } from "react";
import BookSlide from "./BookSlide";
import { Data } from "../Services/Data";
import axios from "axios";
import $ from "jquery";
import { url } from "../Services/Url";

const HighlightedBooks = () => {
  const Data2 = Data.slice(12, 20).map((item) => item);
  const Data1 = Data.slice(0, 12).map((item) => item);
  const [books, setBooks] = useState([]);
  // const [Data, setData] = useState([]);

  useEffect(() => {
    axios
      .get(`${url}/api/data`)
      .then((res) => {
        setBooks(res.data.slice(0, 12));
        // $(".my-carousel").owlCarousel();
      })
      .catch((err) => {
        console.log(err.message);
      });
  }, []);

  // console.log("datas", Data);
  return (
    <>
      <div className="product-area mt-5">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <div
                className="section-title text-center "
                style={{ marginBottom: "50px" }}
              >
                <h2> </h2>
                <p>
                  Browse the collection of our best selling and top interresting
                  products. <br /> ll definitely find what you are looking for..
                </p>
              </div>
            </div>
            <div className="col-lg-12">
              {/* tab-menu-start */}
              <div
                className="tab-menu text-center"
                style={{ marginBottom: "40px" }}
              >
                <ul className="nav justify-content-center">
                  <li>
                    <a className="active" href="#Audiobooks" data-toggle="tab">
                       {" "}
                    </a>
                  </li>
                  <li>
                    <a href="#books" data-toggle="tab">
                       
                    </a>
                  </li>
                  <li>
                    <a href="#bussiness" data-toggle="tab">
                       {" "}
                    </a>
                  </li>
                </ul>
              </div>
              {/*  tab-menu-end */}
            </div>
          </div>
          {/* Audiobooks */}
          <div className="tab-content">
            <div className="tab-pane fade show active" id="Audiobooks">
              <div className="tab-active owl-carousel my-carousel">
                {/* {books.length > 0 &&
                  books.map((item, index) => (
                    <BookSlide
                      key={index}
                      books={item}
                      name={item.title}
                      orginal_price={item.orginal_price}
                      sell_price={item.sell_price}
                    />
                  ))} */}

                {Data1.map((item, index) => (
                  <BookSlide
                    key={index}
                    image={item.image}
                    name={item.book}
                    book={item}
                    orginal_price={item.orginal_price}
                    sell_price={item.sell_price}
                  />
                ))}
              </div>
            </div>
            {/* AudioBooks end */}
            {/* books */}
            <div className="tab-pane fade show " id="books">
              <div className="tab-active owl-carousel ">
                {Data2.map((item, index) => (
                  <BookSlide
                    key={index}
                    image={item.image}
                    name={item.book}
                    book={item}
                    orginal_price={item.orginal_price}
                    sell_price={item.sell_price}
                  />
                ))}
              </div>
            </div>
            {/* Books end */}
            {/* bussiness */}
            <div className="tab-pane fade show " id="bussiness">
              <div className="tab-active owl-carousel">
                {Data1.map((item, index) => (
                  <BookSlide
                    key={index}
                    image={item.image}
                    name={item.book}
                    book={item}
                    orginal_price={item.orginal_price}
                    sell_price={item.sell_price}
                  />
                ))}
              </div>
            </div>
            {/* bussiness end */}
          </div>
        </div>
      </div>
    </>
  );
};

export default HighlightedBooks;
