import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import Pagination from "./Pagination";
import SingleGridBook from "./SingleGridBook";
import { ScaleLoader } from "react-spinners";
import { url } from "../Services/Url";
const TabBook = () => {
  const [books, setBooks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [booksPerPage, setBooksPerPage] = useState(40);

  const { category } = useParams();

  const loaderStyle = { textAlign: "center" };

  useEffect(() => {
    const fetchBooks = async () => {
      setLoading(true);
      await axios
        .get(`${url}/api/data`)
        .then((res) => {
          setBooks(
            category
              ? res.data.filter((item) => item.category === category)
              : res.data
          );
          window.scrollTo(0, 0);
          setLoading(false);
          // $(".my-carousel").owlCarousel();
        })
        .catch((err) => {
          console.log(err.message);
        });
    };

    fetchBooks();
  }, []);

  const indexOfLastPage = currentPage * booksPerPage;
  const indexOfFirstPage = indexOfLastPage - booksPerPage;
  const currentBooks = books.slice(indexOfFirstPage, indexOfLastPage);
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const nextPage = () => setCurrentPage(currentPage + 1);
  const prevPage = () => setCurrentPage(currentPage - 1);

  console.log("cp", currentPage);
  return (
    <>
      {loading ? (
        <div style={loaderStyle}>
          <ScaleLoader color="#24902f" />
        </div>
      ) : (
        <>
          <div className="row">
            {currentBooks.map((item, key) => (
              <SingleGridBook
                name={item.title}
                sell_price={item.sell_price}
                book={item}
                loading={loading}
              />
            ))}
          </div>
          <Pagination
            booksPerPage={booksPerPage}
            totalBooks={books.length}
            loading={loading}
            currentPage={currentPage}
            paginate={paginate}
            nextPage={nextPage}
            prevPage={prevPage}
          />
        </>
      )}
    </>
  );
};

export default TabBook;
