import React from "react";
import { Link } from "react-router-dom";
import TabBook from "./TabBook";
const BookLists = () => {
  return (
    <>
      <div className="col-lg-9 col-md-12 col-12 order-lg-2 order-1">
        <div className="category-image" style={{ marginBottom: "30px" }}>
          <Link to="#">
            <img
              src="/img/Boi-Bazaar/Header-for-book-list-page.jpg"
              alt="banner"
              style={{ width: "100%" }}
            />
          </Link>
        </div>
        <div className="section-title-5" style={{ marginBottom: "30px" }}>
          <h2>Book</h2>
        </div>
        <div className="toolbar" style={{ marginBottom: "30px" }}>
          <div className="shop-tab">
            <div className="tab-3">
              <ul className="nav"></ul>
            </div>
            <div className="list-page">
              {/* <p>(Showing 1 to 60 of 571 books)</p> */}
            </div>
          </div>
        </div>
        <TabBook />
      </div>
    </>
  );
};

export default BookLists;
