import React, { useState, useEffect } from "react";
import { Link, useLocation, useParams } from "react-router-dom";
import FeaturedBooks from "../Components/FeaturedBooks";
import ProductInfo from "./ProductInfo";
import { Link as Link2 } from "react-scroll";
import axios from "axios";
import { Data } from "../Services/Data";
import { url } from "../Services/Url";
import { ScaleLoader } from "react-spinners";
import SingleCategorySlider from "./SingleCategorySlider";
const BookDetails = () => {
  const [value, setValue] = useState(1);
  const { bookname } = useParams();
  const [boi, setBoi] = useState([]);
  const [loading, setLoading] = useState(false);
  const mybookName = bookname.replace(/-/g, " ");

  const relatedProducts = Data.slice(0, 3).map((item, index) => item);
  const relatedProducts2 = Data.slice(3, 6).map((item, index) => item);
  const singleCategory = Data.slice(20, 26).map((item, index) => item);
  const matas = boi.filter((data) => data.title === mybookName);
  const newMatas = matas.length > 1 ? matas.pop() : matas;
  useEffect(() => {
    const fetchBooks = async () => {
      setLoading(true);
      await axios
        .get(`${url}/api/data`, {
          headers: {
            "Access-Control-Allow-Origin": "*",
            "Content-Type": "application/json",
          },
        })
        .then((res) => {
          setBoi(res.data);
          window.scrollTo(0, 0);
          setLoading(false);
        })
        .catch((err) => {
          console.log(err.message);
        });
    };
    fetchBooks();
  }, []);
  const loaderStyle = { textAlign: "center" };

  return (
    <>
      <div className="product-main-area" style={{ marginBottom: "70px" }}>
        <div className="container">
          <div className="row mt-5">
            <div className="col-lg-9 col-md-12 col-12 order-lg-1 order-1">
              {loading ? (
                <div style={loaderStyle}>
                  <ScaleLoader color="#24902f" />
                </div>
              ) : (
                <>
                  <div className="product-main-area">
                    <div className="row">
                      {matas.map((item) => (
                        <>
                          <div className="col-lg-5 col-md-6 col-12">
                            <img src={item.image} alt="" height="379" />
                          </div>
                          <div className="col-lg-7 col-md-6 col-12">
                            <div className="product-info-main">
                              <div className="page-title">
                                <h1>{item.title}</h1>
                              </div>
                              <div className="product-info-stock-sku">
                                <span>by {item.author}</span>
                                <div className="product-attribute">
                                  <span></span>
                                  <span className="value"></span>
                                </div>
                                <p>
                                  <span style={{ fontWeight: "bolder" }}>
                                    Category:
                                  </span>{" "}
                                  {item.category}
                                </p>
                              </div>
                              <div className="product-reviews-summary">
                                <div className="rating-summary">
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </div>
                                <div className="reviews-actions">
                                  <Link2
                                    to="reviews"
                                    spy={true}
                                    smooth={true}
                                    duration={1000}
                                  >
                                    {" "}
                                    5 Reviews{" "}
                                  </Link2>
                                  <Link2
                                    to="reviews"
                                    spy={true}
                                    smooth={true}
                                    duration={1000}
                                    className="view"
                                    style={{ textDecoration: "none" }}
                                  >
                                    Add Your Review
                                  </Link2>
                                </div>
                              </div>
                              <div className="product-info-price">
                                <div className="price-final">
                                  <span>{item.sell_price * value} Tk.</span>
                                  <span className="old-price">
                                    Tk. {item.orginal_price * value}
                                  </span>
                                  <span
                                    style={{ fontSize: "12px" }}
                                    className="text-secondary"
                                  >
                                    {" "}
                                    ( You Save per book TK.{" "}
                                    <span>
                                      {item.orginal_price - item.sell_price}
                                    </span>
                                    ){" "}
                                  </span>
                                </div>
                              </div>
                              <div className="product-add-form">
                                <form action="#">
                                  <div className="quality-button">
                                    <div class="input-group input-group-sm mb-3 mt-2">
                                      <div class="input-group-prepend">
                                        <span
                                          class="input-group-text"
                                          id="inputGroup-sizing-sm"
                                          onClick={() => {
                                            if (value > 1) {
                                              setValue(value - 1);
                                            }
                                          }}
                                          style={{ cursor: "pointer" }}
                                        >
                                          <i class="fas fa-minus"></i>
                                        </span>
                                      </div>
                                      <input
                                        type="text"
                                        class="form-control"
                                        aria-label="Small"
                                        aria-describedby="inputGroup-sizing-sm"
                                        style={{ width: "35px" }}
                                        value={value}
                                        onChange={(e) =>
                                          setValue(e.target.value)
                                        }
                                      />
                                      <div class="input-group-prepend">
                                        <span
                                          class="input-group-text"
                                          id="inputGroup-sizing-sm"
                                          onClick={() => {
                                            setValue(value + 1);
                                          }}
                                          style={{ cursor: "pointer" }}
                                        >
                                          <i class="fas fa-plus"></i>
                                        </span>
                                      </div>
                                    </div>
                                  </div>
                                  <Link
                                    to="#"
                                    style={{ textDecoration: "none" }}
                                  >
                                    Add to cart
                                  </Link>
                                  <Link to="#" style={{ marginLeft: "0.5rem" }}>
                                    <i className="fa fa-heart"></i>
                                  </Link>
                                </form>
                              </div>
                              <div className="product-social-links">
                                <div className="product-addto-links-text">
                                  <p></p>
                                </div>
                              </div>
                            </div>
                          </div>
                        </>
                      ))}
                    </div>
                  </div>

                  <ProductInfo datas={matas} />
                </>
              )}
            </div>

            <div className="col-lg-3 col-md-12 col-12 order-lg-2 order-2">
              <div className="shop-left">
                <div className="left-title" style={{ marginBottom: "20px" }}>
                  <h4>Related Products</h4>
                </div>
                <div
                  className="random-area "
                  style={{ marginBottom: "30px", fontFamily: "Hind Siliguri" }}
                >
                  <div className="product-active-2 owl-carousel">
                    <div className="product-total-2">
                      {relatedProducts.map((item, index) => (
                        <div
                          className="single-most-product bd "
                          style={{ marginBottom: "18px" }}
                        >
                          <div className="most-product-img">
                            <Link to="#">
                              <img src={item.image} alt="book" />
                            </Link>
                          </div>
                          <div className="most-product-content">
                            <div className="product-rating">
                              <ul>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                              </ul>
                            </div>
                            <h4>
                              <Link
                                to={{
                                  pathname: `/product-details/${item.book.replace(
                                    / /g,
                                    "-"
                                  )}`,
                                  // state: { book: props.book },
                                }}
                                onClick={() =>
                                  (window.location.href = `/product-details/${item.book.replace(
                                    / /g,
                                    "-"
                                  )}`)
                                }
                              >
                                {item.book}
                              </Link>
                            </h4>
                            <div className="product-price">
                              <ul>
                                <li>TK. {item.sell_price}</li>
                                <li className="old-price">
                                  Tk. {item.orginal_price}
                                </li>
                              </ul>
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>

                    <div className="product-total-2">
                      {relatedProducts2.map((item, index) => (
                        <div
                          className="single-most-product bd "
                          style={{ marginBottom: "18px" }}
                        >
                          <div className="most-product-img">
                            <Link to="#">
                              <img src={item.image} alt="book" />
                            </Link>
                          </div>
                          <div className="most-product-content">
                            <div className="product-rating">
                              <ul>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                                <li>
                                  <Link to="#">
                                    <i className="fa fa-star"></i>
                                  </Link>
                                </li>
                              </ul>
                            </div>
                            <h4>
                              <Link to="#">{item.book}</Link>
                            </h4>
                            <div className="product-price">
                              <ul>
                                <li>TK. {item.sell_price}</li>
                                <li className="old-price">
                                  Tk. {item.orginal_price}
                                </li>
                              </ul>
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
                <div className="banner-area " style={{ marginBottom: "30px" }}>
                  <div className="banner-img-2">
                    <Link to="#">
                      <img src="/img/Boi-Bazaar/My-wishlist.jpg" alt="banner" />
                    </Link>
                  </div>
                </div>

                <div className="left-title-2">
                  <h2>My Wish List</h2>
                  <p>You have no items in your wish list.</p>
                </div>
              </div>
            </div>
          </div>
          {/* <FeaturedBooks /> */}
          <SingleCategorySlider
            singleCategory={singleCategory}
            title="  "
          />
        </div>
      </div>
    </>
  );
};

export default BookDetails;
