import React from "react";
import { Link } from "react-router-dom";
// import { Data } from "../Services/Data";
const SingleCategorySlider = ({ title, singleCategory }) => {
  // const data = Data.slice(0, 12).map((item) => item);

  return (
    <>
      <div className="single-category-slider mb-5 mt-5">
        <div className="container">
          <div className="row">
            <h5 style={{ fontWeight: "bold", borderBottom: "1px solid #333" }}>
              {title ? title : ""}
            </h5>
            <div className="tab-active owl-carousel">
              {/* <!-- single-product-start --> */}
              {singleCategory.map((item, index) => (
                <div className="product-wrapper">
                  <div className="product-img">
                    <Link to="#">
                      <img src={item.image} alt="book" className="primary" />
                    </Link>
                    <div className="quick-view">
                      <Link
                        className="action-view"
                        to="#"
                        data-target="#productModal"
                        data-toggle="modal"
                        title="Quick View"
                      >
                        <i className="fa fa-search-plus"></i>
                      </Link>
                    </div>
                    <div className="product-flag">
                      <ul>
                        <li>
                          <span className="sale">new</span>
                        </li>
                        <li>
                          <span className="discount-percentage">-5%</span>
                        </li>
                      </ul>
                    </div>
                  </div>
                  <div className="product-details ">
                    <div className="product-rating">
                      <ul>
                        <li>
                          <Link to="#">
                            <i className="fa fa-star"></i>
                          </Link>
                        </li>
                        <li>
                          <Link to="#">
                            <i className="fa fa-star"></i>
                          </Link>
                        </li>
                        <li>
                          <Link to="#">
                            <i className="fa fa-star"></i>
                          </Link>
                        </li>
                        <li>
                          <Link to="#">
                            <i className="fa fa-star"></i>
                          </Link>
                        </li>
                        <li>
                          <Link to="#">
                            <i className="fa fa-star"></i>
                          </Link>
                        </li>
                      </ul>
                    </div>
                    <h4>
                      <Link
                        to={{
                          pathname: `/product-details/${item.book.replace(
                            / /g,
                            "-"
                          )}`,
                          // state: { book: props.book },
                        }}
                        onClick={() =>
                          (window.location.href = `/product-details/${item.book.replace(
                            / /g,
                            "-"
                          )}`)
                        }
                      >
                        {item.book}
                      </Link>
                    </h4>
                    <div className="product-price">
                      <ul>
                        <li>Tk.{item.sell_price}</li>
                      </ul>
                    </div>
                  </div>
                  <div className="product-link">
                    <div className="product-button">
                      <Link to="#" title="Add to cart">
                        <i className="fa fa-shopping-cart"></i>Add to cart
                      </Link>
                    </div>
                    <div className="add-to-link">
                      <ul>
                        <li>
                          <Link to="product-details" title="Details">
                            <i className="fas fa-external-link-alt"></i>
                          </Link>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              ))}
              {/* <!-- single-product-end --> */}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default SingleCategorySlider;
