import React from "react";
import { Link, useLocation } from "react-router-dom";
import { Data } from "../Services/Data";
const BookSlide = (props) => {
  const linkStyle = {
    textDecoration: "none",
  };
  // const location = useLocation();
  // const myLocation = location.pathname;
  // console.log(myLocation);
  // const refreshPage = () => {
  //   window.location.href = { myLocation };
  // };
  return (
    <>
      <div className="tab-total">
        {/* <!-- single-product-start --> */}
        <div className="product-wrapper" style={{ marginBottom: "40px" }}>
          <div className="product-img">
            <Link to="#" style={linkStyle}>
              <img src={props.image} alt="book" className="primary" />
            </Link>
            <div className="quick-view">
              <Link
                className="action-view"
                to="#"
                data-target="#productModal"
                data-toggle="modal"
                title="Quick View"
              >
                <i className="fa fa-search-plus"></i>
              </Link>
            </div>
            <div className="product-flag">
              <ul>
                <li>
                  <span className="sale">New </span>{" "}
                </li>
                <li>
                  <span className="discount-percentage">-5%</span>
                </li>
              </ul>
            </div>
          </div>
          <div className="product-details">
            <h4>
              <Link
                to={{
                  pathname: `/product-details/${props.name.replace(/ /g, "-")}`,
                  // state: { book: props.book },
                }}
                onClick={() =>
                  (window.location.href = `/product-details/${props.name.replace(
                    / /g,
                    "-"
                  )}`)
                }
              >
                {props.name}
              </Link>
            </h4>
            <div className="product-price">
              <ul>
                <li>Tk {props.sell_price}</li>
                <li className="old-price">Tk. {props.orginal_price}</li>
              </ul>
            </div>
          </div>
          <div className="product-link">
            <div className="product-button">
              <Link to="#" title="Add to cart">
                <i className="fa fa-shopping-cart"></i>Add to cart
              </Link>
            </div>
            <div className="add-to-link">
              <ul>
                <li>
                  <Link
                  // to={{
                  //   pathname: `/product-details/${props.book.book.replace(
                  //     / /g,
                  //     "-"
                  //   )}`,
                  //   state: { book: props.book },
                  // }}
                  // title="Details"
                  // onClick={() =>
                  //   (window.location.href = `/product-details/${props.book.book.replace(
                  //     / /g,
                  //     "-"
                  //   )}`)
                  // }
                  >
                    <i className="fas fa-external-link-alt"></i>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
        {/* <!-- single-product-end --> */}
      </div>
    </>
  );
};

export default BookSlide;
