import React, { createRef } from "react";
import HeaderTopBar from "../Components/HeaderTopBar";
import LogoBar from "../Components/LogoBar";
import MenuBar from "../Components/MenuBar";
import HeroSlider from "../Components/HeroSlider";
import HighlightedBooks from "../Components/HighlightedBooks";
import PromotionBanner from "../Components/PromotionBanner";
import BestSeller from "../Components/BestSeller";
import FeaturedBooks from "../Components/FeaturedBooks";
import PromotionBooks from "../Components/PromotionBooks";
import ThreeCatagoryBooks from "../Components/ThreeCatagoryBooks";
import Reviews from "../Components/Reviews";
import InfoBar from "../Components/InfoBar";
import LatestBlog from "../Components/LatestBlog";
import SocialMediaFooter from "../Components/SocialMediaFooter";
import GlobalFooter from "../Components/GlobalFooter";
import TopCategoryBanners from "../Components/TopCategoryBanners";
import SingleCategorySlider from "../Components/SingleCategorySlider";
import Test from "../Components/Test";
import { Data } from "../Services/Data";
import DealsWeek from "../Components/DealsWeek";
import WeeklyTopAuthors from "../Components/WeeklyTopAuthors";
const Home = () => {
  const singleCategory = Data.slice(0, 6).map((item, index) => item);
  const singleCategory2 = Data.slice(15, 21).map((item, index) => item);
  const singleCategory3 = Data.slice(10, 16).map((item, index) => item);
  const singleCategory4 = Data.slice(23, 29)
    .reverse()
    .map((item, index) => item);
  const singleCategory5 = Data.slice(29, 35).map((item, index) => item);
  const singleCategory6 = Data.slice(12, 18).map((item, index) => item);
  const singleCategory7 = Data.slice(14, 20).map((item, index) => item);
  const singleCategory8 = Data.slice(29, 35)
    .reverse()
    .map((item, index) => item);
  const singleCategory9 = Data.slice(18, 24)
    .reverse()
    .map((item, index) => item);
  return (
    <div>
      <HeaderTopBar />
      <LogoBar />
      <MenuBar />
      <HeroSlider />
      <InfoBar />
      {/* <TopCategoryBanners /> */}
      <HighlightedBooks />

      <PromotionBanner />
      <SingleCategorySlider
        title="   "
        singleCategory={singleCategory}
      />
      <SingleCategorySlider title="" singleCategory={singleCategory2} />
      <SingleCategorySlider title="" singleCategory={singleCategory3} />
      <BestSeller />
      {/* <FeaturedBooks /> */}
      {/* <SingleCategorySlider /> */}
      <SingleCategorySlider
        title=" "
        singleCategory={singleCategory4}
      />
      <SingleCategorySlider title="" singleCategory={singleCategory7} />
      <SingleCategorySlider
        title=" "
        singleCategory={singleCategory6}
      />
      <PromotionBooks />
      <SingleCategorySlider
        title=" "
        singleCategory={singleCategory5}
      />
      <SingleCategorySlider
        title="  "
        singleCategory={singleCategory2}
      />
      <SingleCategorySlider
        title="   "
        singleCategory={singleCategory}
      />
      <DealsWeek />
      <SingleCategorySlider
        title=",   "
        singleCategory={singleCategory8}
      />
      <SingleCategorySlider
        title="-   "
        singleCategory={singleCategory2}
      />
      <SingleCategorySlider
        title=" "
        singleCategory={singleCategory9}
      />
      <WeeklyTopAuthors />
      <ThreeCatagoryBooks />
      <Reviews />

      <LatestBlog />

      <GlobalFooter />
    </div>
  );
};

export default Home;
