import React, { useState } from "react";
import { Link } from "react-router-dom";

const ReviewComments = () => {
  const ReadMore = ({ children }) => {
    const text = children;
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
      setIsReadMore(!isReadMore);
    };

    return (
      <p className="text">
        {isReadMore ? text.slice(0, 150) : text}
        <span onClick={toggleReadMore} className="read-or-hide">
          {isReadMore ? (
            <span
              style={{
                color: "#24902f",
                fontWeight: "bold",
                cursor: "pointer",
              }}
            >
              Read more
            </span>
          ) : (
            <span style={{ color: "#2790aa", cursor: "pointer" }}>
              Show Less
            </span>
          )}
        </span>
      </p>
    );
  };
  return (
    <>
      <div className="container mb-3">
        <div className="row" style={{ borderBottom: "1px solid #e5e5e5" }}>
          <div className="col-lg=3">
            <img
              src="/img/men.jpeg"
              alt=""
              style={{
                borderRadius: "50%",
                height: "48px",
                width: "48px",
                display: "block",
              }}
            />
            <span
              className="text-secondary mt-1 lh-1"
              style={{ display: "block" }}
            >
              Micheal Jackson
            </span>
            <span className="text-secondary" style={{ display: "block" }}>
              30 Mar 2021
            </span>
            <div className="rating-summary mb-3">
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
            </div>
          </div>
          <div className="col-lg-8 ml-5 mt-2">
            <ReadMore>
                       
                     ,  
                 ,      
                     -
                       
                        
                       
                         
            </ReadMore>
          </div>
        </div>
      </div>
    </>
  );
};

export default ReviewComments;
