import React from "react";

const CategoryBanner = () => {
  return (
    <>
      <div className="image-container">
        <div
          className="single-slider "
          style={{
            backgroundImage: "url(" + "img/Boi-Bazaar/Header-image.jpg" + ")",
            height: "299px",
            display: "cover",
          }}
        ></div>
        <div className="overlay-img">
          <div className="container">
            <div className="row">
              <div className="col-sm-3"></div>
              <div className="col-sm-6 mt-5">
                <div className="input-group mb-3 mt-5 search-category">
                  <input
                    type="text"
                    className="form-control mt-4"
                    placeholder="Search your Favourite Category"
                    aria-label="Search your Favourite Category"
                    aria-describedby="basic-addon2"
                  />
                  <div className="input-group-append">
                    <input
                      className="btn btn-success mt-4"
                      type="button"
                      value="Search Categories"
                      id="basic-addon2"
                    ></input>
                  </div>
                </div>
              </div>

              <div className="col-sm-3"></div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CategoryBanner;
