import React from "react";
import { Link, useLocation, useParams } from "react-router-dom";
const Breadcumbs = () => {
  const refreshPage = () => {
    window.location.href = "/";
  };
  const location = useLocation();
  let lo = location.pathname;
  const { bookname } = useParams();
  console.log("bookname:", bookname);
  return (
    <>
      <div className="breadcrumbs-area" style={{ marginBottom: "0px" }}>
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              {/* <div className="breadcrumbs-menu">
                <ul>
                  <li>
                    <Link to="/" onClick={refreshPage}>
                      Home
                    </Link>
                  </li>
                  <li>
                    <Link to={lo} className="active">
                      {lo.slice(1).toUpperCase()}
                    </Link>
                  </li>
                </ul>
              </div> */}
              <nav
                aria-label="breadcrumb"
                style={{ backgroundColor: "#fff !important" }}
              >
                <ol className="breadcrumb" style={{ backgroundColor: "#fff" }}>
                  <li className="breadcrumb-item ">
                    <Link
                      to="/"
                      style={{ textDecoration: "none" }}
                      onClick={refreshPage}
                    >
                      Home
                    </Link>
                  </li>
                  <li className="breadcrumb-item  active">
                    {/* <Link to={lo} style={{ textDecoration: "none" }}>
                      {" "}
                      {lo.slice(1).toUpperCase()}
                    </Link> */}
                    {/* <span className="text-secondary">{bookname}</span> */}
                    {bookname !== undefined ? (
                      <span className="text-secondary">{bookname}</span>
                    ) : (
                      <span>{lo.slice(1).toUpperCase()}</span>
                    )}
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Breadcumbs;
