import React from 'react'

const NotFoundList = () => {
    return (
        <>
            <div className="section-element-area " style={{padding:"20px 0"}}>
			<div className="container">
				<div className="row">
					<div className="col-lg-12">
						<div className="entry-header text-center" style={{marginBottom:"20px"}}>
							<img src="/img/3.jpg" alt="not-found-img" />
							<p>Oops! That page cant be found.</p>
						</div>
						<div className="entry-content text-center" style={{marginBottom:"30px"}}>
							<p>Sorry, but the page you are looking for is not found. Please, make sure you have typed the current URL.</p>
							<a href="!#">GO TO HOME</a>
						</div>
					</div>
				</div>
			</div>
		</div>
        </>
    )
}

export default NotFoundList
