import React, { useRef } from "react";
import { Link } from "react-router-dom";
const ReviewRatings = () => {
  return (
    <>
      <div
        data-bs-spy="scroll"
        data-bs-target="#list-example"
        data-bs-offset="0"
        class="scrollspy-example"
        tabindex="0"
      >
        <div className="review-ratings" id="reviews">
          <div className="container">
            <div
              className="row"
              style={{
                borderTop: "1px solid #e5e5e5",
              }}
            >
              <h4 style={{ color: "#333333" }} className="review-title mt-3">
                Reviews and Ratings
              </h4>
            </div>
          </div>
          <div
            className="container mb-3"
            style={{ borderBottom: "1px solid #e5e5e5" }}
          >
            <div className="row">
              <div className="col-lg-1 mt-2">
                <h3>4.53</h3>
              </div>
              <div className="col-lg-5 mt-1">
                <span className="text-secondary " style={{ display: "block" }}>
                  325 Ratings and 142 Reviews
                </span>
                <div className="rating-summary mb-3">
                  <Link to="#">
                    <i className="fa fa-star"></i>
                  </Link>
                  <Link to="#">
                    <i className="fa fa-star"></i>
                  </Link>
                  <Link to="#">
                    <i className="fa fa-star"></i>
                  </Link>
                  <Link to="#">
                    <i className="fa fa-star"></i>
                  </Link>
                  <Link to="#">
                    <i className="fa fa-star"></i>
                  </Link>
                </div>
              </div>
              <div className="col-lg-6">
                <h5 className="text-right">
                  Please login to write review{" "}
                  <button className="btn btn-outline-success ml-3">
                    Login
                  </button>
                </h5>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ReviewRatings;
