import React from "react";
import { Link } from "react-router-dom";
const Reviews = () => {
  return (
    <div>
      <div
        className="testimonial-area  bg"
        style={{
          padding: "100px 0",
          backgroundImage:
            "url(" + "img/Boi-Bazaar/Testimonials-Background.jpg" + ")",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundAttachment: "scroll",
        }}
      >
        <div className="container">
          <div className="row">
            <div className="testimonial-active owl-carousel">
              <div className="col-lg-12">
                <div className="single-testimonial text-center">
                  <div className="testimonial-img">
                    <Link to="#">
                      <i className="fa fa-quote-right"></i>
                    </Link>
                  </div>
                  <div className="testimonial-text">
                    <p>
                      I'm so happy with all of the themes, great support, could
                      not wish for more. These people are <br /> geniuses !
                      Kudo's from the Netherlands !
                    </p>
                    <Link to="#" style={{ textDecoration: "none" }}>
                      Sandy Wilcke/user
                    </Link>
                  </div>
                </div>
              </div>
              <div className="col-lg-12">
                <div className="single-testimonial text-center">
                  <div className="testimonial-img">
                    <Link to="#">
                      <i className="fa fa-quote-right"></i>
                    </Link>
                  </div>
                  <div className="testimonial-text">
                    <p>
                      All Perfect !! I have three sites with magento , this
                      theme is the best !! Excellent support ,<br /> advice
                      theme installation package , sorry for English, are
                      Italian but I had no problem !! Thank you !
                    </p>
                    <Link to="#" style={{ textDecoration: "none" }}>
                      Sandy Wilcke/user
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Reviews;
