import React from "react";
import { Link } from "react-router-dom";
const HeroSlider = () => {

  return (
    <div>
      {/* <div className="slider-area">
        <div className="slider-active owl-carousel"> */}
      <div
        className="single-slider pt-125 pb-130 bg-img"
        style={{
          backgroundImage: "url(" + "img/Boi-Bazaar/Header_2.jpg" + ")",
          height: "599px",
          paddingBottom: "130px",
          paddingTop: "125px",
        }}
      >
        <div className="container">
          <div className="row">
            <div className="col-lg-3 text-left ">
              <div className="illustration">
                  <img src="img/Boi-Bazaar/Illustration.png" 
                  alt="illustration"  
                  style={{
                    position:"absolute",marginLeft:"-80px",
                    display:"inline !important",
                    zIndex:"999",
                    marginTop:"-107px"
                  }}/>
              </div>
            </div>
            <div className="col-lg-8">
              <div className="slider-content " style={{ marginTop: "75px" }}>
                <h3
                  className=""
                  style={{
                    fontSize: "60px",
                    fontWeight: "bold",
                    letterSpacing: "3px",
                    lineHeight: "1.2",
                    marginLeft: "7.5rem",
                    fontFamily: "myFont",
                  }}
                >
                   
                </h3>
                <h1
                  style={{
                    letterSpacing: "4px",
                    fontSize: "100px",
                    fontFamily: "myFont",
                    fontWeight: "bold",
                  }}
                >
                   
                </h1>
                <Link to="#" className="" style={{ marginLeft: "10rem" }}>
                  Shop now
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    //   </div>
    // </div>
  );
};

export default HeroSlider;
