import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import axios from "axios";
import { PublicationsData } from "../Services/PublicationsData";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
const WeeklyTopPublications = () => {
  const [author, setAuthor] = useState([]);
  let location = useLocation();
  let checkLocation = location.pathname;
  useEffect(() => {
    const fetchData = async () => {
      await axios
        .get("https://jsonplaceholder.typicode.com/photos")
        .then((res) => setAuthor(res.data.slice(0, 10)))
        .catch((err) => console.log(err.message));
    };
    fetchData();
  }, []);

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 5,
    arrow: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <>
      <div
        className="recent-post-area mt-5"
        // style={{ paddingTop: "95px", paddingBottom: "100px" }}
      >
        <div className="container">
          <div className="row">
            <div className={checkLocation === "/" ? "col-lg-8" : "col-lg-12"}>
              <div
                className={
                  checkLocation === "/"
                    ? "section-title text-left  section-title-res"
                    : "section-title text-center  section-title-res"
                }
                style={{ marginBottom: "30px" }}
              >
                <h3 style={{ fontWeight: "bold" }}> </h3>
              </div>
            </div>
            <div className={checkLocation === "/" ? "col-lg-4" : "d-none"}>
              <div className="section-title text-right  section-title-res">
                <Link
                  to="/authors"
                  type="button"
                  className="author-view btn btn-outline-success"
                >
                  View All
                </Link>
              </div>
            </div>
          </div>
          <hr />
          <Slider {...settings} className="mt-5">
            {PublicationsData.slice(0, 8).map((data, index) => (
              <>
                <div className="col-12" key={index}>
                  <img
                    className="card-img-top ml-4"
                    src={data.image}
                    alt=""
                    style={{
                      borderRadius: "50%",
                      border: "1px solid #c7c7c7",
                      width: "140px",
                      height: "140px",
                    }}
                  />
                  <div className="card-body text-center">
                    <Link
                      to="#"
                      className="authors-title card-title"
                      style={{ textDecoration: "none", color: "#333" }}
                    >
                      {data.author}
                    </Link>
                  </div>
                </div>
              </>
            ))}
          </Slider>
        </div>
      </div>
    </>
  );
};

export default WeeklyTopPublications;
