import React from "react";
import { Link } from "react-router-dom";
const LogoBar = () => {
  const refreshPage = () => {
    window.location.href = "/";
  };

  return (
    <>
      <div
        className="logobar "
        style={{
          padding: "20px",
          backgroundImage: "url(" + "/img/banner/27.jpg" + ")",
          backgroundPosition: "center",
          backgroundAttachment: "scroll",
          backgroundRepeat: "repeat",
        }}
      >
        <div className="container">
          <div className="row">
            <div className="col-lg-3 col-md-3 col-12 logo-img">
              <div className="logo-area logo-xs-mrg">
                <Link to="/" onClick={refreshPage}>
                  <img src="/img/logo/BoiBazaar.png" alt="logo" width="226" />
                </Link>
              </div>
            </div>
            <div className="col-lg-4 col-md-4 col-12">
              <div className="header-search">
                <form action="#">
                  <input
                    type="text"
                    placeholder="Search entire Book here..."
                    style={{ outline: "none" }}
                  />
                  <Link to="#">
                    <i className="fa fa-search"></i>
                  </Link>
                </form>
              </div>
            </div>
            {/* <div className="col-lg-1 col-md-1 col-12">
              <div className="logo-area text-center logo-xs-mrg mt-3">
                <Link to="#" style={{ textDecoration: "none" }}>
                  <i
                    className="fas fa-phone-alt"
                    style={{ fontSize: "30px", display: "inline" }}
                    data-toggle="tooltip"
                    data-placement="top"
                    title="HotLine : 0123654789"
                  ></i>
                </Link>
              </div>
            </div> */}

            <div className="col-lg-2 col-md-2 col-12 text-right">
              <div className="logo-area  logo-xs-mrg mt-3">
                <Link to="#" style={{ textDecoration: "none" }}>
                  <i className="fas fa-heart" style={{ fontSize: "30px" }}></i>
                </Link>
              </div>
            </div>
            <div className="col-lg-2 col-md-2 col-12 text-right">
              <div className="my-cart">
                <ul>
                  <li>
                    <Link to="#">
                      <i className="fa fa-shopping-cart"></i>
                    </Link>
                    <span>2</span>
                    <div className="mini-cart-sub ">
                      <div className="cart-product">
                        <div className="single-cart">
                          <div className="cart-img">
                            <Link to="#">
                              <img src="/img/product/1.jpg" alt="book" />
                            </Link>
                          </div>
                          <div className="cart-info">
                            <h5>
                              <Link to="#" style={{ textDecoration: "none" }}>
                                Joust Duffle Bag
                              </Link>
                            </h5>
                            <p>1 x 60.00</p>
                          </div>
                          <div className="cart-icon">
                            <Link to="#">
                              <i className="fa fa-minus-circle"></i>
                            </Link>
                          </div>
                        </div>
                        <div className="single-cart">
                          <div className="cart-img">
                            <Link to="#">
                              <img src="/img/product/3.jpg" alt="book" />
                            </Link>
                          </div>
                          <div className="cart-info">
                            <h5>
                              <Link to="#" style={{ textDecoration: "none" }}>
                                Chaz Kangeroo Hoodie
                              </Link>
                            </h5>
                            <p>1 x 52.00</p>
                          </div>
                          <div className="cart-icon">
                            <Link to="#">
                              <i className="fa fa-minus-circle"></i>
                            </Link>
                          </div>
                        </div>
                      </div>
                      <div className="cart-totals">
                        <h5>
                          Total <span>12.00</span>
                        </h5>
                      </div>
                      <div className="cart-bottom">
                        <Link
                          className="view-cart"
                          to="#"
                          style={{ textDecoration: "none" }}
                        >
                          view cart
                        </Link>
                        <Link to="#" style={{ textDecoration: "none" }}>
                          Check out
                        </Link>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>

            {/* <div className="col-lg-1 col-md-1 col-12 text-right">
              <div className="logo-area  logo-xs-mrg mt-3">
                <Link to="#" style={{ textDecoration: "none" }}>
                  <i className="fas fa-user" style={{ fontSize: "30px" }}></i>
                </Link>
              </div>
            </div> */}
          </div>
        </div>
      </div>
    </>
  );
};

export default LogoBar;
