import React from "react";
import { Link } from "react-router-dom";
const PromotionBooks = () => {
  return (
    <>
      <div
        className="banner-static-area bg "
        style={{
          padding: "100px 0",
          backgroundImage:
            "url(" + "img/Boi-Bazaar/Promotional-Campaign-Background.jpg" + ")",
          backgroundPosition: "center",
          backgroundAttachment: "scroll",
          backgroundRepeat: "repeat",
        }}
      >
        <div className="container">
          <div className="row">
            <div className="col-lg-6 col-md-6 col-12">
              <div className="banner-shadow-hover xs-mb">
                <Link to="#">
                  <img
                    src="img/Boi-Bazaar/Promotional-Campaign-of-Book-1.jpg"
                    style={{ maxWidth: "100%" }}
                    alt="banner"
                  />
                </Link>
              </div>
            </div>
            <div className="col-lg-6 col-md-6 col-12">
              <div className="banner-shadow-hover">
                <Link to="#">
                  <img
                    src="img/Boi-Bazaar/Promotional-Campaign-of-Book-2.jpg"
                    style={{ maxWidth: "100%" }}
                    alt="banner"
                  />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default PromotionBooks;
