import React from "react";
// import { Link } from "react-router-dom";
const ProductInfo = ({ datas }) => {
  return (
    <>
      <div className="product-info-area" style={{ marginTop: "80px" }}>
        <ul className="nav">
          <li>
            <a
              className="active"
              href="#specification"
              data-toggle="tab"
              style={{ textDecoration: "none" }}
            >
              Specification
            </a>
          </li>
          <li>
            <a
              className=""
              href="#author"
              data-toggle="tab"
              style={{ textDecoration: "none" }}
            >
              Author
            </a>
          </li>
          {/* <li>
            <a
              href="#Reviews"
              data-toggle="tab"
              style={{ textDecoration: "none" }}
            >
              Reviews
            </a>
          </li> */}
        </ul>
        <div className="tab-content">
          <div className="tab-pane fade show active" id="specification">
            <div className="valu">
              <table class="table table-bordered">
                {datas.map((item) => (
                  <>
                    <tbody>
                      <tr>
                        <th
                          style={{ backgroundColor: "#d9f2d8" }}
                          width="20%"
                          scope="row"
                        >
                          Title
                        </th>
                        <td>{item.title}</td>
                      </tr>
                      <tr>
                        <th style={{ backgroundColor: "#d9f2d8" }} scope="row">
                          Author
                        </th>
                        <td>{item.author}</td>
                      </tr>
                      <tr>
                        <th style={{ backgroundColor: "#d9f2d8" }} scope="row">
                          Publisher
                        </th>
                        <td>{item.publisher}</td>
                      </tr>
                      {/* <tr>
                        <th style={{ backgroundColor: "#d9f2d8" }} scope="row">
                          Edition
                        </th>
                        <td>1st Published,2021</td>
                      </tr> */}
                      {item["number-of-pages"] ? (
                        <tr>
                          <th
                            style={{ backgroundColor: "#d9f2d8" }}
                            scope="row"
                          >
                            Number of Pages
                          </th>
                          <td>{item["number-of-pages"]}</td>
                        </tr>
                      ) : (
                        ""
                      )}

                      <tr>
                        <th style={{ backgroundColor: "#d9f2d8" }} scope="row">
                          Country
                        </th>
                        <td>{item.country}</td>
                      </tr>
                      <tr>
                        <th style={{ backgroundColor: "#d9f2d8" }} scope="row">
                          Language
                        </th>
                        <td>{item.language}</td>
                      </tr>
                    </tbody>
                  </>
                ))}
              </table>
            </div>
          </div>
          <div className="tab-pane fade " id="author">
            {datas.map((item) => (
              <>
                <div className="valu">
                  <h6>{item.author}</h6>
                </div>
              </>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductInfo;
