import React from "react";
import { Link } from "react-router-dom";
import { Data } from "../Services/Data";
const DealsWeek = () => {
  const bestSeller1 = Data.slice(0, 2).map((item, index) => item);
  const bestSeller2 = Data.slice(2, 4).map((item, index) => item);
  const bestSeller3 = Data.slice(4, 6).map((item, index) => item);
  return (
    <>
      <div className="bestseller-area mt-5" style={{ paddingBottom: "100px" }}>
        <div className="container">
          <div className="row">
            <div className="col-lg-8 col-md-12 col-12">
              <div className="bestseller-content">
                <h1 style={{ fontWeight: "bold" }}> Deals of the week</h1>
                <h2>
                  Inspiration
                  <br />
                  Speaks
                </h2>
                <p className="categories">
                  categories:
                  <Link to="#" style={{ textDecoration: "none" }}>
                    Books
                  </Link>{" "}
                  ,{" "}
                  <Link to="#" style={{ textDecoration: "none" }}>
                    Authors
                  </Link>
                </p>
                <p>
                  Vestibulum porttitor iaculis gravida. Praesent vestibulum
                  varius placerat. Cras tempor congue neque, id aliquam orci
                  finibus sit amet. Fusce at facilisis arcu.{" "}
                </p>
                <div className="social-author">
                  <ul>
                    <li>
                      <Link to="#">
                        <i className="fab fa-facebook-f"></i>
                      </Link>
                    </li>{" "}
                    <li>
                      <Link to="#">
                        <i className="fab fa-twitter"></i>
                      </Link>
                    </li>
                  </ul>
                </div>
              </div>
              <div className="banner-img-2">
                <Link to="#">
                  <img
                    src="img/Boi-Bazaar/Deals_of_the_week.jpg"
                    alt="banner"
                  />
                </Link>
              </div>
            </div>
            <div className="col-lg-4 col-md-12 col-12">
              <div className="bestseller-active owl-carousel">
                <div className="bestseller-total">
                  {bestSeller1.map((item, key) => (
                    <>
                      <div
                        className="single-bestseller"
                        style={{ marginBottom: "25px" }}
                      >
                        <div className="bestseller-img">
                          <Link to="#">
                            <img
                              src={item.image}
                              alt="book"
                              style={{ width: "80%" }}
                            />
                          </Link>
                          <div className="product-flag">
                            <ul>
                              <li>
                                <span className="sale">new</span>
                              </li>
                              <li>
                                <span className="discount-percentage">-5%</span>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="bestseller-text mt-1 ">
                          <h3 style={{ fontSize: "12px" }}>
                            {" "}
                            <Link to="#">{item.book}</Link>
                          </h3>
                          <div className="price">
                            <ul>
                              <li>
                                <span className="new-price">
                                  Tk. {item.sell_price}
                                </span>
                              </li>
                              <li>
                                <span className="old-price">
                                  Tk. {item.orginal_price}
                                </span>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </>
                  ))}
                </div>
                <div className="bestseller-total">
                  {bestSeller2.map((item, key) => (
                    <>
                      <div
                        className="single-bestseller"
                        style={{ marginBottom: "25px" }}
                      >
                        <div className="bestseller-img">
                          <Link to="#">
                            <img
                              src={item.image}
                              alt="book"
                              style={{ width: "80%" }}
                            />
                          </Link>
                          <div className="product-flag">
                            <ul>
                              <li>
                                <span className="sale">new</span>
                              </li>
                              <li>
                                <span className="discount-percentage">-5%</span>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="bestseller-text ">
                          <h3>
                            {" "}
                            <Link to="#">{item.book}</Link>
                          </h3>
                          <div className="price">
                            <ul>
                              <li>
                                <span className="new-price">
                                  Tk. {item.sell_price}
                                </span>
                              </li>
                              <li>
                                <span className="old-price">
                                  Tk. {item.orginal_price}
                                </span>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </>
                  ))}
                </div>

                <div className="bestseller-total">
                  {bestSeller3.map((item, key) => (
                    <>
                      <div
                        className="single-bestseller"
                        style={{ marginBottom: "25px" }}
                      >
                        <div className="bestseller-img">
                          <Link to="#">
                            <img
                              src={item.image}
                              alt="book"
                              style={{ width: "80%" }}
                            />
                          </Link>
                          <div className="product-flag">
                            <ul>
                              <li>
                                <span className="sale">new</span>
                              </li>
                              <li>
                                <span className="discount-percentage">-5%</span>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="bestseller-text ">
                          <h3>
                            {" "}
                            <Link to="#">{item.book}</Link>
                          </h3>
                          <div className="price">
                            <ul>
                              <li>
                                <span className="new-price">
                                  Tk. {item.sell_price}
                                </span>
                              </li>
                              <li>
                                <span className="old-price">
                                  Tk. {item.orginal_price}
                                </span>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default DealsWeek;
